<template lang="en">
<section>
    <div class="col-md-12 p-0 mt-2">
        <div class="theme-primary partition-full">
            <span class="p-name text-white">{{ $t('Timesheet') }}</span>
            <span class="p-actions float-right">
                <a id="AddManually" @click="exportToExcel" href="javascript:;" class="p-action-btn text-white "><em class="fa fa-list"></em><br /><span>{{ $t('Export') }}</span></a>
                <a id="AddManually" href="javascript:;" class="p-action-btn text-white "><em class="fa fa-calendar"></em><br /><span>{{ $t('thisWeek') }}</span></a>
                <a :class="sendForApprovalButton ? '' : 'tt_disabled'" v-if="listCheckbox.length > 0" class="p-action-btn text-white" id="DeleteMultiple" title="" href="javascript:;" data-toggle-tooltip="tooltip" data-original-title="Delete" @click="deleteMultipleRow">
                    <i class="fa fa-trash"></i><br><span>Delete</span>
                </a>

                <!-- <button class="fa fa-trash-o text-danger"></button> -->
            </span>
        </div>
    </div>

    <div class="listing py-3">
        <loader :is-visible="isLoading"></loader>
        <dynamic-form class="daterangecontrolHS" lang="en" v-if="DataLoaded" :buttons="buttons1" :schema="FormSchema" v-on:OnSubmit="onSubmit">
            <template v-slot:tgslot-slotdate="{ data }">
                <!--<div class="col-12 input-group timesheetdatepicker bg-white border-0 p-0">
                        <div class="form-control py-0 px-2">                            
                            <input id="date_range" v-model="dateselect" value="2023/12/12 - 2023/12/12" name="date_range"
                                type="date" @input="GetStartEndDates"
                                class="textfield2 ie_crossicon_none ToDate form-control p-1 hasdatetimepicker notvue"
                                autocomplete="off" data-toggle="datetimepicker" :title="$t('DateRange')"
                                :placeholder="$t('DateRange')">
                        </div>
                    </div> -->
                <DatePicker class="d-flex col-12 input-group timesheetdatepicker bg-white p-0" v-model="dateselect" :popover="false" @input="GetStartEndDates">
                    <template #default="{ togglePopover, inputValue, inputEvents }">
                        <a id="aDateLable" class="pr-2 text-dark bg-white py-1 float-left" style="cursor:default">{{
                                $t('DateRange') }}: {{ DisplayWeekDate }}</a>
                        <input id="date_range" class="form-control datepicker_HS" :value="inputValue" v-on="inputEvents" />
                        <div class="input-group-append cursor-pointer" v-on:click="togglePopover">
                            <span class="input-group-text">
                                <em class="fa fa-calendar"></em>
                            </span>
                        </div>

                    </template>
                </DatePicker>
            </template>
            <template v-slot:tgslot-slotbutton="{ data }">
                <button class="btn btn-success" @click="FetchData" :title="$t('ShowTimeSheet')">{{ $t('ShowTimeSheet')
                    }} </button>
            </template>
            <template v-slot:tgslot-slotstartenddate="{ data }">
                <p style="top:-74px;margin-left: 104px;">{{ DisplayWeekDate }}</p>
            </template>
        </dynamic-form>
        <!-- <dynamic-form lang="en" v-if="DataLoaded" :schema="ListSchema" v-on:OnSubmit="onSubmit">
                <template v-slot:tgslot-TimeSheetTable="{ data }">
                    <a id="btnAddNewEntry" class="btn btn-success formbtn mr-2 text-dark" v-on:click="showAddTaskModal"
                        name="btnAddNewEntry" title='Add New Entry' style="float: right;"><i class="fa fa-plus pr-2"></i>Add
                        New Entry</a>
                    -->
        <div class="listing">
            <div class="col-10 mb-2 float-left p-0">
                <div class="mr-2 d-inline-block">
                    <span class="legends-purple colorboxsquare"></span>{{ $t('AuditableRecords') }}
                </div>
                <div class="mr-2 d-inline-block">
                    <span class="legends_profile colorboxsquare"></span> {{ $t('LeaveHoliday') }}
                </div>
                <div class="mr-2 d-inline-block">
                    <span class="legends_low colorboxsquare"></span>{{ $t('UnPaidLeaveRecords') }}
                </div>
                <!-- <div class="mr-2 d-inline-block">
                        <span class="legends_medium colorboxsquare"></span> {{$t('TimeSheet')}}
                    </div> -->
                <div class="mr-2 d-inline-block">
                    <span class="legends_high colorboxsquare"></span>{{ $t('HolidayLeaveAuditableRecords') }}
                </div>
                <div class="mr-2 d-inline-block">
                    <span class="lg-pausedresumed colorboxsquare"></span>{{ $t('PausedResumedTrackEntries') }}
                </div>
            </div>
            <div class="col-2 mb-2 float-right p-0">
                <a :class="sendForApprovalButton ? '' : 'tt_disabled'" id="btnAddNewEntry" class="btn btn-primary formbtn" v-on:click="showAddTaskModal" name="btnAddNewEntry" :title="$t('AddNewEntry')" style="float: right;"><em class="fa fa-plus pr-2"></em>{{ $t('AddNewEntry') }}</a>
                <div class="hs_tag3" v-if="addTaskShow" ref="hs_tag3">
                    <AddTaskTimeSheetModal v-bind:store="store" v-on:close="addTaskShow = false" v-on:saveTaskEntry="saveTaskEntry"></AddTaskTimeSheetModal>
                </div>

            </div>

        </div>
        <div class="table-responsive">
            <table class="table table-bordered dt-responsive nowrap mt-0 timesheettable" id="tableData">
                <thead class="thead-bg">
                    <tr>
                        <th class="remove_td align-middle">
                            <div class="form-check form-check-inline">
                                <div class="custom-control custom-checkbox">
                                    <input :disabled="sendForApprovalButton ? false : true" type="checkbox" :value="'all'" :class="{ 'chkItems custom-control-input': true }" name="DeleteInputs" :id="'allCheck'" @change="allCheckBox" v-model="allCheck" style="display: none;">
                                    <label class="custom-control-label universal-custom-control-label pt-1" :for="'allCheck'"></label>
                                </div>

                            </div>
                        </th>
                        <th class="remove_td align-middle d-none">{{ $t('ProjectId') }} </th>
                        <th style="min-width: 200px;">{{ $t('ProjectName') }}</th>
                        <th class="remove_td align-middle d-none">{{ $t('TaskId') }}</th>
                        <th class="align-middle d-none">{{ $t('TaskName') }}</th>
                        <th v-for="(item, index) in HeaderList" :key="index">
                            {{ getPreviousDay(item) }}
                        </th>
                        <th id="RowTotal">{{ $t('Total') }}</th>
                        <th class="remove_td ">{{ $t('Action') }}</th>
                    </tr>
                </thead>
                <tbody v-if="computedTimesheetList.length > 0">

                    <tr v-for="(dataItem, dataIndex) in computedTimesheetList" :key="dataIndex">
                        <td class="remove_td tabchild" style="max-width:10px;">

                            <div class="form-check form-check-inline">
                                <div class="custom-control custom-checkbox">
                                    <input :disabled="sendForApprovalButton ? false : true" type="checkbox" class="list-checkBox" :class="{ 'chkItems custom-control-input': true }" :value="dataIndex" name="DeleteInputs" :id="'chk_' + dataIndex" @change="selectedValues" v-model="listCheckbox" style="display: none;">
                                    <label class="custom-control-label universal-custom-control-label pt-1" :for="'chk_' + dataIndex"></label>
                                </div>

                            </div>
                        </td>
                        <!-- :class="{ 'table-danger': dataItem['IsValid'] == false }" -->
                        <td class="remove_td align-middle d-none">{{ dataItem['ProjectId'] }}</td>
                        <!-- <td>{{ dataItem['ProjectName'] }}</td> -->
                        <td class="its_projectnamebase">

                            <span class="float-left w-90">
                                <!-- <a title="" value="-1" style="cursor:default;" class="text-dark" href="#"> -->
                                <span class="font-14 font-weight-medium float-left text-align-left d-flex w-100 its_projectname">
                                    {{ dataItem['ProjectName'] }}:
                                    <span class="text-dark d-block its_taskname ml-1 font-weight-normal">{{
                                            dataItem['TaskDetail'] }}</span>
                                </span>
                                <span class="text-dark d-block font-14 its_taskdesc text-truncate float-left font-weight-medium w-100">{{
                                            dataItem.TaskDescription }}</span>
                                <span v-if="indexEdit === dataIndex + 1">
                                    <input @change="editChange(dataItem, dataIndex + 1, $event)" type="text" :value="dataItem['TaskDescription']" @blur="Edit(dataIndex + 1)" /></span>
                                <!-- </a> -->
                            </span>
                            <a v-if="sendForApprovalButton" class="float-right" @click="Edit(dataIndex + 1)"><em class="fa fa-pencil"></em></a>

                        </td>
                        <td class="remove_td align-middle d-none">{{ dataItem['TaskId'] }}</td>
                        <td class="align-middle d-none">{{ dataItem['TaskDetail'] }}</td>
                        <td class="align-middle" v-for="(item, index) in HeaderList" :key="index" :class="{ 'table-danger': dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item } +' ' + checkLegendsDates(item)" :title="dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item ? dataItem['IsValid'].Status : ''">
                            <!-- :class="{ 'table-danger': dataItem['IsValid'] !=null && dataItem['IsValid'] !=1 && IsValidDate == item }" -->
                            <input placeholder="00:00" class="form-control" type="text" v-model="dataItem[item]" @input="ChangeCurrentDateTime(dataItem, item)" @blur="validateTimeInput(dataItem, item)">
                            <div class="d-none">{{ dataItem[item] }} </div>
                        </td>
                        <td class="align-middle">{{ rowSum(dataItem) }}</td>
                        <td class="remove_td single-action text-center"><a><i class="fa fa-times text-danger" @click="SingleDeleteData(dataIndex)"></i></a> </td>
                    </tr>

                    <tr class="its_fixedrow" v-for="(dataItem, dataIndex) in computedBreakList" :key="dataIndex">
                        <!-- :class="{ 'table-danger': dataItem['IsValid'] == false }" -->
                        <td class="remove_td align-middle "></td>
                        <td class=" align-middle d-none">{{ dataItem['ProjectId'] }}</td>
                        <!-- <td>{{ dataItem['ProjectName'] }}</td> -->
                        <td class="align-middle font-weight-medium">{{ dataItem['ProjectName'] }}</td>
                        <td class="remove_td align-middle d-none">{{ dataItem['TaskId'] }}</td>
                        <td class="remove_td align-middle d-none">{{ dataItem['TaskDetail'] }}</td>
                        <td class="align-middle" v-for="(item, index) in HeaderList" :key="index" :class="{ 'table-danger': dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item }" :title="dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item ? dataItem['IsValid'].Status : ''">
                            <!-- :class="{ 'table-danger': dataItem['IsValid'] !=null && dataItem['IsValid'] !=1 && IsValidDate == item }" -->
                            <input placeholder="00:00" @blur="validateTimeInput(dataItem, item)" class="form-control" type="text" v-model="dataItem[item]" @input="ChangeCurrentDateTime(dataItem, item)">
                            <div class="d-none">{{ dataItem[item] }}</div>
                        </td>
                        <td class="align-middle">{{ rowSum(dataItem) }}</td>
                        <td class="remove_td single-action text-center"><i class="fa fa-times text-danger d-none" @click="SingleDeleteData(dataIndex)"></i> </td>
                    </tr>
                </tbody>
                <tbody v-else>
                    <tr>
                        <td colspan="8" class="text-center">{{ $t('NoRecordfound') }}</td>
                    </tr>
                </tbody>
                <tfoot v-if="computedTimesheetList.length > 0">
                    <div style=" display: contents; float: left;width: 100%;">
                        <tr class="its_fixedrow2">
                            <td class="remove_td align-middle d-none"></td>
                            <td class="align-middle "></td>
                            <td>{{ $t('Total') }}:</td>
                            <td class="remove_td align-middle d-none"></td>
                            <td class="remove_td align-middle d-none"></td>
                            <td v-for="(tData, index) in totalData" :key="index">{{ tData.value }}</td>
                            <td>{{ SumOfFooterRows }}</td>
                            <td class="remove_td"></td>
                        </tr>
                    </div>
                </tfoot>
            </table>
        </div>
        <div class="col-lg-12 col-sm-12 p-0 mt-2">
            <a v-if="sendForApprovalButton" id="btnSendForApproval" class="btn btn-success formbtn text-light" v-on:click="SaveAsDraft(0, 1)" name="" :title="$t('SendForApproval')" style="float: right;"><i class="fa fa-send pr-2"></i>{{
                            $t('SendForApproval') }} </a>
            <!-- <a id="btnCancel" title="Cancel" class="btn btn-danger d-none"><i class="fa fa-close pr-2"></i><span>Cancel</span></a> -->
            <a v-if="copyToOtherUserButton" id="btnCopyLastWeek" isapprover="0" class="btn btn-warning formbtn mr-2 text-dark" v-on:click="showCopyTimesheet" name="" :title="$t('CopyToOtherUser')" style="float: right;"><i class="fa fa-copy pr-2"></i>{{
                        $t('CopyToOtherUser') }} </a>

            <a v-if="copyLastWeekButton" id="btnCopyLastWeek" isapprover="0" class="btn btn-warning formbtn mr-2 text-dark" v-on:click="CopyLastWeek" name="" :title="$t('CopyLastWeek')" style="float: right;"><i class="fa fa-copy pr-2"></i>{{
                        $t('CopyLastWeek') }} </a>

            <a v-if="saveAsDraftButton" id="btnSaveAsDraft" isapprover="0" class="btn btn-warning formbtn text-dark mr-2" v-on:click="SaveAsDraft(1, 0)" name="" :title="$t('SaveAsDraft')" style="float: right;"><i class="fa fa-save pr-2"></i>{{ $t('SaveAsDraft')
                    }}</a>
            <!-- <a id="btnSendForApproval" isapprover="0" class="btn btn-success text-white formbtn mr-2" onclick="" title='SendForApproval'><i class="fa fa-send pr-2"></i>SendForApproval</a> -->
        </div>
    </div>

    <copyTimesheet v-if="openModal" v-on:close="openModal = false"></copyTimesheet>

</section>
</template>

<script>
import DataService from '../../services/DataService';
import {
    getPreviousDay,
    getDateInFormat
} from "@/assets/common/vue-common.js"
import AddTaskTimeSheetModal from "../TimeTrack/modals/AddTaskTimesheetModal.vue"
import DatePicker from 'v-calendar/lib/components/date-picker.umd'
import copyTimesheet from "../../components/Modal/Timesheet/CopyTimesheet.vue"
import {
    userTimerInfo
} from "../../stores/TimeTrack"
export default {
    components: {
        AddTaskTimeSheetModal,
        DatePicker,
        copyTimesheet
    },
    setup() {
        const store = userTimerInfo()

        return {
            store
        }
    },
    data() {
        return {
            saveAsDraftButton: true,
            copyLastWeekButton: true,
            copyToOtherUserButton: true,
            sendForApprovalButton: true,
            getPreviousDay,
            dateselect: '',
            //formData: [],
            deletedArray: [],
            totalData: [],
            DataLoaded: 0,
            noRecord: false,
            TotalRecords: 0,
            isLoading: false,
            TimesheetList: [],
            BreakList: [],
            LeaveList: [],
            HeaderList: [],
            UserId: null,
            StartDate: null,
            EndDate: null,
            DisplayWeekDate: "This Week",
            StartWeekDay: null,
            totalSeconds: 0,
            //Headers: [],
            addTaskShow: false,
            openModal: false,
            SumOfRows: [],
            SumOfFooterRows: 0,
            allCheck: '',
            listCheckbox: [],
            FormSchema: [{
                layoutType: "triple",
                Data: [{
                        astype: "SlotField",
                        label: "",
                        name: "slotdate",
                        value: "",
                        placeholder: "Date Range",
                    },
                    {
                        astype: "SelectField",
                        label: "User",
                        name: "user_id",
                        value: "",
                        placeholder: "",
                        
                        config: {
                            options: [],
                            onChange: this.FetchData
                        },
                        validationRules: {
                            //"required": true
                        },
                    },
                    {
                        astype: "SlotField",
                        label: "",
                        name: "slotbutton",
                        value: "",
                        placeholder: "",

                    },
                ]
            }],
            IsValidDate: [],
            isedit: false,
            indexEdit: '',
            carryItems: [],
            oldTaskDesc: [],
            isOldTaskDesc: [],
            privilegeParams: null,
            holidaydates: []
        }
    },
    created: async function () {
        var VueObj = this;
        this.privilegeParams = `controller=Timesheet&action=`;
        this.DataLoaded = 1;
        await VueObj.GetStartEndDatesNew();
        await VueObj.GetUsersForTimesheet();
        VueObj.FormSchema[0].Data[1].value = VueObj.GetUserInfo.ID;
        await VueObj.FetchData();
        //VueObj.checkHolidayDate()
        //VueObj.buttonShowHide()

    },
    mounted: async function () {
        var VueObj = this;
        //VueObj.SaveAsDraft();
    },
    computed: {
        computedTimesheetList() {
            return this.TimesheetList
        },
        computedBreakList() {
            return this.BreakList
        },
        computedLeaveList() {
            return this.LeaveList
        },
        filteredDeletedArray() {
            return this.deletedArray;
        },
        rowSums() {
            return this.TimesheetList.map(dataItem => this.rowSum(dataItem));
        },
        footerSums() {
            return this.totalData.map(dataItem => this.footerSums(dataItem));
        }
    },
    methods: {
        async checkHolidayDate() {
            var VueObj = this;
            var fDate = VueObj.StartDate;
            var tDate = VueObj.EndDate;
            VueObj.holidaydates = []
            var url = `dateFrom=${fDate}&dateTo=${tDate}`
            const {
                data
            } = await DataService.GetHolidayDate(url)
            VueObj.holidaydates = data
        },
        checkLegendsDates(dates) {
            let matchDates = this.holidaydates.find((element) => getDateInFormat(element.DAY_DATE) == getDateInFormat(dates))
            return matchDates ? 'legend_leaves' : ''
        },
        validateTimeInput(data,item) {
            
      var regex = /^([01][0-9]|2[0-3]):[0-5][0-9]$/; 
      if (!regex.test(data[item])) {
        this.ShowAlert(this.$t("validTime"), "warning", true, "Alert");
        //alert("Please enter time in HH:MM .");
        data[item] = ""; 
      }
    },

        buttonShowHide() {
            var VueObj = this;
            var fDate = VueObj.StartDate;
            var tDate = VueObj.EndDate;
            var url = `dateFrom=${fDate}&dateTo=${tDate}&selectedUserId=${VueObj.FormSchema[0].Data[1].value}`
            DataService.GetTimesheetStatus(url).then((res) => {
                if (!res.data) {
                    VueObj.saveAsDraftButton = true
                    VueObj.copyLastWeekButton = true
                    VueObj.copyToOtherUserButton = true
                    VueObj.sendForApprovalButton = true

                } else {
                    VueObj.saveAsDraftButton = false
                    VueObj.copyLastWeekButton = false
                    VueObj.copyToOtherUserButton = false
                    VueObj.sendForApprovalButton = false
                    //VueObj.ShowAlert(VueObj.$t(res.data), "warning", true, "Alert");

                }
            })
        },
        exportToExcel() {
            var tab_text = '<table border="1px" style="font-size:20px" ">';
            var txtArea1;
            var j = 0;
            var tab = document.getElementById('tableData');
            var tfoot = tab.getElementsByTagName('tfoot') // id of table
            var lines = tab.rows.length;
            // the first headline of the table
            if (lines > 0) {
                tab_text = tab_text + '<tr bgcolor="#DFDFDF">' + tab.rows[0].innerHTML + '</tr>';
            }
            for (j = 1; j < lines; j++) {
                tab_text = tab_text + "<tr>" + tab.rows[j].innerHTML + "</tr>";
            }
            tab_text = tab_text +  tfoot[0].innerHTML  ;
            tab_text = tab_text + "</table>";
            tab_text = tab_text.replace(/<th class="remove_td">[^>]*>|<\/th>/gi, "");
            tab_text = tab_text.replace(/<th class="remove_td[^>]*>|<\/th>/gi, "");
            
            tab_text = tab_text.replace(/<A[^>]*>|<\/A>/g, "");
            tab_text = tab_text.replace(/<img[^>]*>/gi, "");
            tab_text = tab_text.replace(/<input[^>]*>|<\/input>/gi, "");
            tab_text = tab_text.replace(/<td class="remove_td[^>]*>|<\/td>/gi, "");
            var ua = window.navigator.userAgent;
            var msie = ua.indexOf("MSIE ");
            var sa;
            // if Internet Explorer
            if (msie > 0 || !!navigator.userAgent.match(/Trident.*rv\:11\./)) {
                txtArea1.document.open("txt/html", "replace");
                txtArea1.document.write(tab_text);
                txtArea1.document.close();
                txtArea1.focus();
                sa = txtArea1.document.execCommand("SaveAs", true, "TimeSheetExport.xls");
            } else // other browser not tested on IE 11
                sa = window.open('data:application/vnd.ms-excel,' + encodeURIComponent(tab_text));

            return (sa);
        },
        allCheckBox() {
            //this.allCheck = "";
            this.listCheckbox = []
            if (this.allCheck) {
                var inputElements = document.getElementsByClassName('list-checkBox');
                for (var i = 0; inputElements[i]; ++i) {
                    this.listCheckbox.push(i)
                }

            } else {
                this.listCheckbox = []
            }
        },
        selectedValues(e) {
            if (!e.target.checked) {
                this.allCheck = false
            } else {
                var inputElements = document.getElementsByClassName('list-checkBox');
                let check = true
                for (var i = 0; inputElements[i]; ++i) {
                    if (!inputElements[i].checked) {
                        check = false
                    }
                }
                if (check) {
                    this.allCheck = true
                }
            }

        },
        async deleteMultipleRow() {
            var vm = this;
            vm.confirmR(this.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("entry")), async function () {
                let listDataArray = []
                vm.listCheckbox.map((item) => {
                    if (vm.computedTimesheetList[item].IsAddNewEntry) {

                        vm.computedTimesheetList.splice(item, 1)
                    } else {
                        listDataArray.push(vm.computedTimesheetList[item])

                    }
                })

                listDataArray.forEach((item, index) => {
                    delete item['Type']
                })
                if (listDataArray.length > 0) {
                    let obj = {
                        deletedData: listDataArray,
                        userId: vm.FormSchema[0].Data[1].value,
                    }
                    const {
                        data
                    } = await DataService.DeleteTimeSheetRecord(obj)
                    console.log("delete", data)
                    if (data === 0) {
                        vm.ShowAlert(vm.$t('Delete_record'), "success", true, "Alert");
                        vm.listCheckbox = []
                        vm.FetchData()

                    }
                }
            })
        },
        footerSums(columnName) {
            const excludedColumns = ["ProjectId", "ProjectName", "TaskDetail", "TaskId"];
            let totalSeconds = 0;

            if (!excludedColumns.includes(columnName)) {
                this.dataArray.forEach((dataItem) => {
                    const value = dataItem[columnName];
                    if (/^([01]\d|2[0-3]):([0-5]\d)$/.test(value)) {
                        const [hours, minutes] = this.getHoursAndMinutes(value);
                        totalSeconds += hours * 3600 + minutes * 60;
                    }
                });
            }

            const totalHours = Math.floor(totalSeconds / 3600);
            const totalMinutes = Math.floor((totalSeconds % 3600) / 60);

            return String(totalHours).padStart(2, "0") + ":" + String(totalMinutes).padStart(2, "0");
        },
        columnSums() {
            const excludedColumns = ["ProjectId", "ProjectName", "TaskDetail", "TaskId"];
            const columnSums = {};

            this.HeaderList.forEach((header) => {
                if (!excludedColumns.includes(header)) {
                    const columnValues = this.TimesheetList.map((dataItem) => {
                        const value = dataItem[header];
                        return /^([01]\d|2[0-3]):([0-5]\d)$/.test(value) ? this.getHoursAndMinutes(value) : 0;
                    });

                    const totalSeconds = columnValues.reduce((sum, [hours, minutes]) => sum + hours * 3600 + minutes * 60, 0);
                    const totalHours = Math.floor(totalSeconds / 3600);
                    const totalMinutes = Math.floor((totalSeconds % 3600) / 60);

                    columnSums[header] = String(totalHours).padStart(2, "0") + ":" + String(totalMinutes).padStart(2, "0");
                }
            });

            return columnSums;
        },
        rowSum(dataItem) {
            var rowcount = 0;
            return Object.values(dataItem).filter(dataItem => dataItem !== "ProjectId" && dataItem !== "ProjectName" && dataItem !== "TaskDetail" && dataItem != "TaskId" && dataItem !== "RowTotal")
                .reduce((sum, value) => {
                    const isDatetimeColumn = /^([01]\d|2[0-3]):([0-5]\d)$/.test(value);
                    if (isDatetimeColumn) {
                        let [hours, minutes] = value != null ? value.split(":") : "00:00".split(":");
                        rowcount += parseInt(hours) * 3600 + parseInt(minutes) * 60;
                    }
                    let hours = Math.floor(rowcount / 3600);
                    let minutes = Math.floor((rowcount % 3600) / 60);
                    sum = String(hours).padStart(2, "0") + ":" + String(minutes).padStart(2, "0");

                    return sum;
                }, 0);
