            VueObj.DisplayWeekDate = "This Week";

        },
        FetchData: async function () {
            //// alert()
            //debugger
            this.indexEdit = '';
            var VueObj = this;
            VueObj.isLoading = true;
            var fDate = VueObj.StartDate;
            var tDate = VueObj.EndDate;
            var inputData = []
            // var url = `userId=${VueObj.GetUserInfo.ID}&dateFrom=${fDate}&dateTo=${tDate}&userId=${VueObj.FormSchema[0].Data[1].value}&companyId=270936`;
            var url = `companyId=${VueObj.GetUserInfo.CompanyId}&userId=${VueObj.FormSchema[0].Data[1].value}&dateFrom=${fDate}&dateTo=${tDate}`;
            VueObj.buttonShowHide()
            DataService.ListTimesheetForWeek(url).then(response => {
                if (response.data != null && response.data != "" && response.data.length > 0) {
                    let headerList1 = Object.keys(response.data[0]).filter(key => key !== 'Type' && key !== 'ProjectId' && key !== 'ProjectName' && key !== 'TaskId' && key !== 'TaskDetail' && key != 'TaskDescription');
                    VueObj.isLoading = false;
                    var count;
                    this.HeaderList = headerList1;

                    VueObj.TotalRecords = response.data.length;
                    VueObj.TimesheetList = response.data.filter((item) => item.Type == 'TimeTrack');
                    VueObj.BreakList = response.data.filter((item) => item.Type == 'Break');
                    VueObj.LeaveList = response.data.filter(item => item.Type == 'Leave');

                    VueObj.TimesheetList.map((item, index) => {

                        //this.isOldTaskDesc[index] = true;
                        this.$set(item, 'RowNumber', index + 1);
                        item.OldTaskDescription = item.TaskDescription
                        return item
                    });

                    console.log('With Row Number', VueObj.TimesheetList);

                    var scol = 0;
                    var totalSeconds = 0;
                    VueObj.SumRowColTotalList()
                    VueObj.checkHolidayDate()
                } else {
                    VueObj.noRecord = true;
                    VueObj.isLoading = false;
                    VueObj.TimesheetList = [];
                }

            });
        },
        SumRowColTotalList: async function () {
            var VueObj = this;
            VueObj.totalData = [];
            //var sum1 = 0;
            VueObj.HeaderList.forEach((column) => {
                var colcount = 0;
                if (column.COLUMN_NAME !== 'ProjectName' && column.COLUMN_NAME !== 'TaskDetail' && column.COLUMN_NAME !== 'TaskId' && column.COLUMN_NAME !== 'ProjectId') {
                    VueObj.TimesheetList.forEach((data, index) => {
                        //scol += value;
                        // const [hours, minutes] = data[column.COLUMN_NAME] != null ? data[column.COLUMN_NAME].split(":") : "00:00".split(":");
                        const [hours, minutes] = data[column] != null ? data[column].split(":") : "00:00".split(":");
                        colcount += parseInt(hours != null && hours.trim() === '' ? 0 : hours) * 3600 + parseInt(minutes != null && minutes.trim() === '' ? 0 : minutes) * 60;

                    });
                    VueObj.BreakList.forEach((data, index) => {
                        const [hours, minutes] = data[column] != null ? data[column].split(":") : "00:00".split(":");
                        colcount += parseInt(hours != null && hours.trim() === '' ? 0 : hours) * 3600 + parseInt(minutes != null && minutes.trim() === '' ? 0 : minutes) * 60;

                    });
                    VueObj.LeaveList.forEach((data, index) => {
                        const [hours, minutes] = data[column] != null ? data[column].split(":") : "00:00".split(":");
                        colcount += parseInt(hours != null && hours.trim() === '' ? 0 : hours) * 3600 + parseInt(minutes != null && minutes.trim() === '' ? 0 : minutes) * 60;

                    });
                    let hours = Math.floor(colcount / 3600);
                    let minutes = Math.floor((colcount % 3600) / 60);
                    var formattedTime = String(hours).padStart(2, "0") + ":" + String(minutes).padStart(2, "0");

                }
                VueObj.totalData.push({
                    value: formattedTime
                });
                VueObj.totalSeconds = colcount;
            });
            var footerSum = 0;
            VueObj.totalData.forEach((item, index) => {
                let [hours, minutes] = item.value != null ? item.value.split(":") : "00:00".split(":");
                footerSum += parseInt(hours) * 3600 + parseInt(minutes) * 60;
                //footerSum = footerSum + item.value;
            });
            let hours = Math.floor(footerSum / 3600);
            let minutes = Math.floor((footerSum % 3600) / 60);
            VueObj.SumOfFooterRows = String(hours).padStart(2, "0") + ":" + String(minutes).padStart(2, "0");
            //VueObj.FetchRowColTotalList();

        },
        SingleDeleteData: function (index) {
            var vm = this;
            var fDate = vm.StartDate;
            var tDate = vm.EndDate;

            vm.TimesheetList.forEach((item) => {
                //debugger
                var iserowxist = vm.TimesheetList.indexOf(item);

                if (iserowxist == index) { // && vm.TimesheetList.splice(index).length > 0
                    if (!item.IsAddNewEntry) {
                        vm.deletedArray.push(item);
                    }
                    vm.TimesheetList.splice(index, 1);
                    this.SumRowColTotalList();
                    this.SumOfRows();
                    this.SumOfFooterRows();
                }

            });
        },
        SaveAsDraft: async function (isDraft, isSendForApproval) {
            this.indexEdit = '';
            var VueObj = this;
            VueObj.isLoading = true

            // this.oldTaskDesc.forEach((descData) => {

            // });

            VueObj.TimesheetList.map((item, index) => {

                if (item.IsAddNewEntry) {
                    let entryData = false
                    let entries = Object.entries(item)
                    entries.map(([key, val] = entry) => {
                        if (key == 'IsAddNewEntry' || key == 'OldTaskDescription' || key == 'ProjectId' || key == 'ProjectName' || key == 'RowNumber' || key == 'TaskDescription' || key == 'TaskId' || key == 'TaskDetail') {
                            console.log('IsAddNewEntry', key)
                        } else {
                            //console.log('key',val)
                            if (val != null) {
                                entryData = true
                                console.log('key', val)
                            }
                        }

                        //console.log('val',val)
                    })

                    if (!entryData) {
                        VueObj.TimesheetList.splice(index, 1);
                    }

                } else {
                    // this.oldTaskDesc.forEach((descData) => {
                    //     if (descData.itemIndex == index + 1) {
                    //         this.$set(item, 'OldTaskDescription', descData.discription);
                    //     }
                    // });

                }
                delete item['Type']
                delete item['IsValid']
                return item

            });

            VueObj.deletedArray.forEach((item, index) => {
                delete item['Type']
                delete item['IsValid']
                return item
            });

            // this.TimesheetList.forEach((item,index) => {
            //     this.$set(item, 'RowNumber', index+1);
            // });

            let obj = {
                TimesheetList: VueObj.TimesheetList,
                deletedData: VueObj.deletedArray,
                breakData: VueObj.BreakList,
                userId: VueObj.FormSchema[0].Data[1].value,
                dateFrom: VueObj.StartDate,
                dateTo: VueObj.EndDate,
                isDraft: isDraft,
                isSendForApproval: isSendForApproval
            }
            //var formData = new FormData();
            //formData.append("jsonData", JSON.stringify(obj));

            await DataService.SaveTimeSheetEntry(obj).then(response => {
                VueObj.TimesheetList = [];
                VueObj.deletedArray = [];
                VueObj.BreakList = [];
                if (response.data != null && response.data != "" && response.data.length > 1) {
                    VueObj.TimesheetList = response.data;

                    VueObj.TimesheetList.forEach((item) => {
                        item.IsValid = JSON.parse(item.IsValid);
                    });
                    VueObj.SumRowColTotalList()
                } else if (response.data.length == 1) {
                    if (response.data[0].Status_Code == 0) {
                        if (isSendForApproval != true) {
                            this.ShowAlert(this.$t("TimeSheetSaved"), 'success', true, this.$t('Alert'));
                            VueObj.TimesheetList = [];
                            VueObj.FetchData();
                        } else if (isSendForApproval == true) {
                            var fDate = this.StartDate;
                            var tDate = this.EndDate;
                            var tDate = this.EndDate;
                            let url = `dateFrom=${fDate}&dateTo=${tDate}&selectedUserId=${this.FormSchema[0].Data[1].value}`
                            DataService.SendForApproval(url).then(response => {

                                if (response.data[0].Status_Code == 'SUCCESS') {
                                    VueObj.TimesheetList = [];
                                    VueObj.FetchData();
                                    //alert()
                                    this.ShowAlert(this.$t("TimesheetSendForApproval"), 'success', false, this.$t('Alert'));
                                } else if (response.data[0].Status_Code == '-3') {
                                    this.ShowAlert(this.$t("EtreesApprovedpending"), 'success', true, this.$t('Alert'));
                                    this.FetchData();
                                } else if (response.data[0].Status_Code == '-4') {
                                    this.ShowAlert(this.$t("TimerRunning"), 'success', true, this.$t('Alert'));
                                    this.FetchData();
                                } else if (response.data[0].Status_Code == '-205') {
                                    this.ShowAlert(this.$t("TimesheetEntryAlreadySaved"), 'success', true, this.$t('Alert'));
                                } else {
                                    this.ShowAlert(this.$t("Something_Wrong"), 'failure', true, this.$t('Alert'));
                                }

                            });
                        }

                    } else {
                        if (response.data[0].Status_Code == '-205') {
                            this.ShowAlert(this.$t("TimesheetEntryAlreadySaved"), 'warning', true, this.$t('Alert'));
                        } else {
                            this.ShowAlert(this.$t("Something_Wrong"), 'failure', true, this.$t('Alert'));
                        }
                    }
                } else {
                    this.ShowAlert(this.$t("Something_Wrong"), 'failure', true, this.$t('Alert'));
                }
            });
            VueObj.isLoading = false
        },
        // CopyLastWeek: async function () {
        //     // if (!GetPrivilege("/Timesheet/PasteLastWeekTimesheet", true, false)) {
        //     //     return false;
